"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { AxiosError } from "axios";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface Transaction {
    id: number;
    reference: string;
    type: string;
    amount: number;
    status: string;
    created_at: string;
}

export default function WalletPage() {
    const [balance, setBalance] = useState(0);
    const [transactions, setTransactions] = useState<Transaction[]>([]);
    const [amount, setAmount] = useState("");
    const [message, setMessage] = useState("");

    useEffect(() => {
        const fetchWallet = async () => {
            try {
                const res = await api.get('/wallet');
                setBalance(res.data.balance);
                setTransactions(res.data.transactions.data);
            } catch (e) {
                console.error(e);
            }
        };
        fetchWallet();
    }, []);

    const handleTopUp = async (e: React.FormEvent) => {
        e.preventDefault();
        setMessage("");
        try {
            const res = await api.post('/wallet/topup', { amount });
            // In a real Paystack flow, we would redirect to res.authorization_url
            // Here we might simulate success if the backend mock allows, or if we used the direct API call that returns the auth URL.

            // For now, let's assume we get a URL or success message. 
            // If we are simulating full Paystack, we need to redirect user to the Paystack checkout page.
            // Since this is a test, I'll display the link or message.

            if (res.data.authorization_url) {
                window.location.href = res.data.authorization_url;
            } else {
                setMessage("Top up initiated. Please complete payment.");
            }
        } catch (e) {
            const error = e as AxiosError<{ message: string }>;
            setMessage("Top up failed: " + (error.response?.data?.message || "Error"));
        }
    };

    return (
        <div className="space-y-6">
            <div className="grid gap-4 md:grid-cols-2">
                <Card>
                    <CardHeader>
                        <CardTitle>Current Balance</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="text-3xl font-bold">GHS {balance}</div>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader>
                        <CardTitle>Top Up Wallet</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <form onSubmit={handleTopUp} className="flex gap-4">
                            <Input
                                type="number"
                                placeholder="Amount"
                                value={amount}
                                onChange={(e) => setAmount(e.target.value)}
                                required
                                min="1"
                            />
                            <Button type="submit">Pay</Button>
                        </form>
                        {message && <p className="mt-2 text-sm text-blue-600">{message}</p>}
                    </CardContent>
                </Card>
            </div>

            <div className="bg-white rounded-lg border shadow-sm">
                <div className="p-4 border-b bg-gray-50">
                    <h3 className="font-semibold">Transaction History</h3>
                </div>
                <div className="p-0">
                    <table className="w-full text-sm text-left">
                        <thead className="bg-gray-50 text-gray-500 uppercase">
                            <tr>
                                <th className="px-6 py-3">Reference</th>
                                <th className="px-6 py-3">Type</th>
                                <th className="px-6 py-3">Amount</th>
                                <th className="px-6 py-3">Status</th>
                                <th className="px-6 py-3">Date</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-200 bg-white">
                            {transactions.map((trx) => (
                                <tr key={trx.id}>
                                    <td className="px-6 py-4">{trx.reference}</td>
                                    <td className="px-6 py-4 capitalize">{trx.type}</td>
                                    <td className="px-6 py-4 font-bold">
                                        {trx.type === 'credit' ? '+' : '-'} {trx.amount}
                                    </td>
                                    <td className="px-6 py-4">{trx.status}</td>
                                    <td className="px-6 py-4">{new Date(trx.created_at).toLocaleDateString()}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}
